	export default {
		name: "notice",
		data() {
			return {
				autoplay: true,
				interval: 2000,
				duration: 500
			}
		},
		methods: {
        changeAutoplay(e) {
            this.autoplay = !this.autoplay
        },
        intervalChange(e) {
            this.interval = e.target.value
        },
        durationChange(e) {
            this.duration = e.target.value
        }
		}

	}
